.header {
  display: flex;
  justify-content: space-evenly;
  width: 100%;
  height: 150px;
  position: absolute;
  top: 1%;
  -webkit-animation: headerAni 3s ease;
          animation: headerAni 3s ease;
}

@-webkit-keyframes headerAni {
  from {
    top: -100px;
  }
  to {
    top: 10px;
  }
}

@keyframes headerAni {
  from {
    top: -100px;
  }
  to {
    top: 10px;
  }
}

.titleHP {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: white;
  font-family: "Raleway", sans-serif;
  font-size: 65px;
  overflow: hidden;
  border-right: 0.15em solid #932426; /* The typwriter cursor */
  white-space: nowrap; /* Keeps the content on a single line */
  margin: 0 auto; /* Gives that scrolling effect as the typing happens */
  -webkit-animation: typing 3s steps(40, end), blink-caret 0.75s step-end infinite;
          animation: typing 3s steps(40, end), blink-caret 0.75s step-end infinite;
}

@-webkit-keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 60%;
  }
}

@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 60%;
  }
}

@-webkit-keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: #932426;
  }
}

@keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: #932426;
  }
}

