  }

  .titleHP {
    font-size: 30px;
    overflow: hidden;
    border-right: none; /* The typwriter cursor */
    white-space: normal; /* Keeps the content on a single line */
    margin: 0 auto; /* Gives that scrolling effect as the typing happens */
    -webkit-animation: none;
            animation: none;
    width: 90%;
    margin-left: 20px;
  }

  .header {
    -webkit-animation: none;
            animation: none;
  }

}

.checkbox {
